import {Component} from "react";
import Navbar from "react-bulma-components/lib/components/navbar";
import Icon from "react-bulma-components/lib/components/icon";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import faAsterisk from "@fortawesome/fontawesome-free-solid/faAsterisk";
import faArchive from "@fortawesome/fontawesome-free-solid/faArchive";
import faLock from "@fortawesome/fontawesome-free-solid/faLock";
import faCopyright from "@fortawesome/fontawesome-free-solid/faCopyright";
import React from "react";

class MenuNavbar extends Component {

  state = { active : false }

  handleClick = () => {
    const { active } = this.state;
    this.setState({ active: !active });
  }
  render() {
    return (
      <Navbar active={this.state.active} onClick={this.handleClick}>
        <Navbar.Burger/>
        <Navbar.Menu>
          <Navbar.Container>
            <Navbar.Item href="/aktuell">
              <Icon style={{'margin-right': '5px'}}>
                <FontAwesomeIcon align="left" icon={faAsterisk}/>
              </Icon>
              Aktuell
            </Navbar.Item>
            <Navbar.Item href="/archiv">
              <Icon style={{'margin-right': '5px'}}>
                <FontAwesomeIcon align="left" icon={faArchive}/>
              </Icon>
              Archiv
            </Navbar.Item>
          </Navbar.Container>
          <Navbar.Container position="end">
            <Navbar.Item href="/datenschutz">
              <Icon style={{'margin-right': '5px'}}>
                <FontAwesomeIcon align="left" icon={faLock}/>
              </Icon>
              Datenschutz
            </Navbar.Item>
            <Navbar.Item href="/impressum">
              <Icon style={{'margin-right': '5px'}}>
                <FontAwesomeIcon align="left" icon={faCopyright}/>
              </Icon>
              Impressum
            </Navbar.Item>
          </Navbar.Container>
        </Navbar.Menu>
      </Navbar>
    )
  }
}

export default MenuNavbar
