import React from "react"
import Tag from "react-bulma-components/lib/components/tag";

const NewsletterTitle = (node) => {
  return (
    <div className={"newsletter-title"}>
      <small className={"date"}>{new Intl.DateTimeFormat('de-DE', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit'
      }).format(new Date(node.node.published))}</small>
      <h3 className={"h3"}>{node.node.title}</h3>{" "}
      <div className={"parts"}>
        {node.node.newsletter.parts.map((part, index) => (
            <span>{part.title} {part.emoji.icon}&nbsp;</span>
          )
        )}
      </div>
      <Tag.Group>
        {node.node.tags.map((tag, index) => (
          <Tag key={tag}>#{tag}</Tag>
        ))}
      </Tag.Group>
    </div>
  )
}

export default NewsletterTitle
